/**
 * DateTimePicker - /  
 */

import React, { useState, useRef, useCallback, useMemo } from 'react';
import { Calendar, X } from 'lucide-react';
import type { DateTimePickerProps, CalendarViewMode } from '../types';
import { CalendarHeader, CalendarGrid, MonthGrid, YearGrid, TimeSelector, CalendarPopover } from '../base';
import { formatDate } from '../utils/formatUtils';
import { defaultLocale, mergeLocale } from '../utils/localeUtils';
import { getDecadeStart, addMonths, addYears, setTimeToDate, getTimeFromDate } from '../utils/dateUtils';
import styles from '../styles/Calendar.module.css';

export const DateTimePicker: React.FC<DateTimePickerProps> = ({
  value,
  onChange,
  size = 'md',
  disabled = false,
  readOnly = false,
  error = false,
  className,
  inline = false,
  locale: localeProp,
  minDate,
  maxDate,
  filterDate,
  clearable = true,
  onClose,
  onOpen,
  id,
  name,
  placeholder,
  required,
  ariaLabel,
  ariaDescribedBy,
  onFocus,
  onBlur,
  dateFormat,
  timeIntervals = 30,
  minTime,
  maxTime,
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [inputValue, setInputValue] = useState('');
  const triggerRef = useRef<HTMLDivElement>(null);
  const inputRef = useRef<HTMLInputElement>(null);

  //  
  const [selectedDate, setSelectedDate] = useState<Date | null>(value ?? null);

  //  
  const [viewMode, setViewMode] = useState<CalendarViewMode>('day');
  const [currentDate, setCurrentDate] = useState(value || new Date());

  // 
  const locale = useMemo(() => mergeLocale(defaultLocale, localeProp), [localeProp]);
  const format = dateFormat || locale.dateTimeFormat;

  //  
  React.useEffect(() => {
    if (value) {
      setInputValue(formatDate(value, format));
      setSelectedDate(value);
    } else {
      setInputValue('');
      setSelectedDate(null);
    }
  }, [value, format]);

  //  /
  const handleOpen = useCallback(() => {
    if (disabled || readOnly) return;
    setIsOpen(true);
    setViewMode('day');
    if (value) {
      setCurrentDate(value);
    }
    onOpen?.();
  }, [disabled, readOnly, value, onOpen]);

  const handleClose = useCallback(() => {
    setIsOpen(false);
    onClose?.();
  }, [onClose]);

  //  
  const handleDateSelect = useCallback((date: Date) => {
    if (viewMode === 'day') {
      //   
      let newDate = date;
      if (selectedDate) {
        const time = getTimeFromDate(selectedDate);
        newDate = setTimeToDate(date, time);
      }
      setSelectedDate(newDate);
      onChange?.(newDate);
      setInputValue(formatDate(newDate, format));
    } else if (viewMode === 'month') {
      setCurrentDate(date);
      setViewMode('day');
    } else if (viewMode === 'year') {
      setCurrentDate(date);
      setViewMode('month');
    }
  }, [viewMode, selectedDate, onChange, format]);

  //  
  const handleTimeSelect = useCallback((date: Date) => {
    //    
    const time = getTimeFromDate(date);
    const baseDate = selectedDate || new Date();
    const newDate = setTimeToDate(baseDate, time);

    setSelectedDate(newDate);
    onChange?.(newDate);
    setInputValue(formatDate(newDate, format));
  }, [selectedDate, onChange, format]);

  // 
  const handleClear = useCallback((e: React.MouseEvent) => {
    e.stopPropagation();
    setSelectedDate(null);
    onChange?.(null);
    setInputValue('');
    inputRef.current?.focus();
  }, [onChange]);

  //  
  const handleApply = useCallback(() => {
    if (!inline) {
      handleClose();
    }
  }, [inline, handleClose]);

  // 
  const handlePrevious = useCallback(() => {
    if (viewMode === 'day') {
      setCurrentDate((prev) => addMonths(prev, -1));
    } else if (viewMode === 'month') {
      setCurrentDate((prev) => addYears(prev, -1));
    } else {
      setCurrentDate((prev) => addYears(prev, -10));
    }
  }, [viewMode]);

  const handleNext = useCallback(() => {
    if (viewMode === 'day') {
      setCurrentDate((prev) => addMonths(prev, 1));
    } else if (viewMode === 'month') {
      setCurrentDate((prev) => addYears(prev, 1));
    } else {
      setCurrentDate((prev) => addYears(prev, 10));
    }
  }, [viewMode]);

  //  
  const inputClassName = [
    styles.input,
    size === 'sm' && styles['input--sm'],
    size === 'lg' && styles['input--lg'],
    error && styles['input--error'],
  ].filter(Boolean).join(' ');

  //  
  const calendarClassName = [
    styles.calendar,
    size === 'sm' && styles['calendar--sm'],
    size === 'lg' && styles['calendar--lg'],
  ].filter(Boolean).join(' ');

  //   
  const renderCalendarContent = () => (
    <div className={styles.dateTimeContainer}>
      <div className={calendarClassName}>
        <CalendarHeader
          currentDate={currentDate}
          viewMode={viewMode}
          onViewModeChange={setViewMode}
          onPrevious={handlePrevious}
          onNext={handleNext}
          minDate={minDate}
          maxDate={maxDate}
          locale={locale}
          size={size}
        />

        {viewMode === 'day' && (
          <CalendarGrid
            currentDate={currentDate}
            selectedDate={selectedDate}
            onSelect={handleDateSelect}
            minDate={minDate}
            maxDate={maxDate}
            filterDate={filterDate}
            locale={locale}
            size={size}
          />
        )}

        {viewMode === 'month' && (
          <MonthGrid
            currentYear={currentDate.getFullYear()}
            selectedDate={selectedDate}
            onSelect={handleDateSelect}
            minDate={minDate}
            maxDate={maxDate}
            locale={locale}
            size={size}
          />
        )}

        {viewMode === 'year' && (
          <YearGrid
            currentDecade={getDecadeStart(currentDate.getFullYear())}
            selectedDate={selectedDate}
            onSelect={handleDateSelect}
            minDate={minDate}
            maxDate={maxDate}
            size={size}
          />
        )}

        {/*  */}
        <div className={styles.footer}>
          <button
            type="button"
            className={styles.footerButton}
            onClick={handleClose}
          >
            
          </button>
          <button
            type="button"
            className={`${styles.footerButton} ${styles['footerButton--primary']}`}
            onClick={handleApply}
          >
            
          </button>
        </div>
      </div>

      {/*   */}
      <TimeSelector
        value={selectedDate}
        onSelect={handleTimeSelect}
        timeIntervals={timeIntervals}
        minTime={minTime}
        maxTime={maxTime}
        use24Hour
        size={size}
      />
    </div>
  );

  //  
  if (inline) {
    return (
      <div className={`${styles.inline} ${className || ''}`}>
        {renderCalendarContent()}
      </div>
    );
  }

  return (
    <div className={`${styles.inputWrapper} ${className || ''}`} ref={triggerRef}>
      <input
        ref={inputRef}
        type="text"
        id={id}
        name={name}
        className={inputClassName}
        value={inputValue}
        readOnly
        onClick={handleOpen}
        onFocus={() => {
          handleOpen();
          onFocus?.();
        }}
        onBlur={onBlur}
        placeholder={placeholder || format}
        disabled={disabled}
        required={required}
        aria-label={ariaLabel}
        aria-describedby={ariaDescribedBy}
        aria-expanded={isOpen}
        aria-haspopup="dialog"
      />

      {clearable && value && !disabled && !readOnly && (
        <button
          type="button"
          className={styles.clearButton}
          onClick={handleClear}
          aria-label="/ "
          tabIndex={-1}
        >
          <X size={10} />
        </button>
      )}

      <span className={styles.inputIcon}>
        <Calendar size={16} />
      </span>

      <CalendarPopover
        isOpen={isOpen}
        triggerRef={triggerRef as React.RefObject<HTMLElement>}
        onClose={handleClose}
      >
        {renderCalendarContent()}
      </CalendarPopover>
    </div>
  );
};

export default DateTimePicker;
