/**
 * DatePicker -   
 */

import React, { useState, useRef, useCallback, useMemo } from 'react';
import { Calendar, X } from 'lucide-react';
import type { DatePickerProps, CalendarViewMode } from '../types';
import { CalendarHeader, CalendarGrid, MonthGrid, YearGrid, CalendarPopover } from '../base';
import { useCalendarState } from '../hooks';
import { formatDate, parseDate } from '../utils/formatUtils';
import { defaultLocale, mergeLocale } from '../utils/localeUtils';
import { getDecadeStart, addMonths, addYears } from '../utils/dateUtils';
import styles from '../styles/Calendar.module.css';

export const DatePicker: React.FC<DatePickerProps> = ({
  value,
  onChange,
  size = 'md',
  disabled = false,
  readOnly = false,
  error = false,
  className,
  inline = false,
  locale: localeProp,
  minDate,
  maxDate,
  filterDate,
  clearable = true,
  onClose,
  onOpen,
  id,
  name,
  placeholder,
  required,
  ariaLabel,
  ariaDescribedBy,
  onFocus,
  onBlur,
  dateFormat,
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [inputValue, setInputValue] = useState('');
  const triggerRef = useRef<HTMLDivElement>(null);
  const inputRef = useRef<HTMLInputElement>(null);

  //  
  const locale = useMemo(() => mergeLocale(defaultLocale, localeProp), [localeProp]);

  // 
  const format = dateFormat || locale.dateFormat;

  //  
  const calendar = useCalendarState({
    initialDate: value,
    onChange: (date) => {
      onChange?.(date);
      if (date) {
        setInputValue(formatDate(date, format));
      } else {
        setInputValue('');
      }
      if (!inline) {
        setIsOpen(false);
        onClose?.();
      }
    },
  });

  //   
  const [viewMode, setViewMode] = useState<CalendarViewMode>('day');
  const [currentDate, setCurrentDate] = useState(value || new Date());

  //   
  const selectedDate = value;

  //  
  React.useEffect(() => {
    if (value) {
      setInputValue(formatDate(value, format));
    } else {
      setInputValue('');
    }
  }, [value, format]);

  //  /
  const handleOpen = useCallback(() => {
    if (disabled || readOnly) return;
    setIsOpen(true);
    setViewMode('day');
    if (value) {
      setCurrentDate(value);
    }
    onOpen?.();
  }, [disabled, readOnly, value, onOpen]);

  const handleClose = useCallback(() => {
    setIsOpen(false);
    onClose?.();
  }, [onClose]);

  //  
  const handleSelect = useCallback((date: Date) => {
    if (viewMode === 'day') {
      onChange?.(date);
      setInputValue(formatDate(date, format));
      if (!inline) {
        handleClose();
      }
    } else if (viewMode === 'month') {
      setCurrentDate(date);
      setViewMode('day');
    } else if (viewMode === 'year') {
      setCurrentDate(date);
      setViewMode('month');
    }
  }, [viewMode, onChange, format, inline, handleClose]);

  // 
  const handleClear = useCallback((e: React.MouseEvent) => {
    e.stopPropagation();
    onChange?.(null);
    setInputValue('');
    inputRef.current?.focus();
  }, [onChange]);

  //  
  const handleInputChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = e.target.value;
    setInputValue(newValue);

    //   
    const parsed = parseDate(newValue, format);
    if (parsed) {
      onChange?.(parsed);
      setCurrentDate(parsed);
    }
  }, [format, onChange]);

  //  
  const handleInputBlur = useCallback(() => {
    //      
    if (inputValue && !parseDate(inputValue, format)) {
      if (value) {
        setInputValue(formatDate(value, format));
      } else {
        setInputValue('');
      }
    }
    onBlur?.();
  }, [inputValue, format, value, onBlur]);

  // 
  const handlePrevious = useCallback(() => {
    if (viewMode === 'day') {
      setCurrentDate((prev) => addMonths(prev, -1));
    } else if (viewMode === 'month') {
      setCurrentDate((prev) => addYears(prev, -1));
    } else {
      setCurrentDate((prev) => addYears(prev, -10));
    }
  }, [viewMode]);

  const handleNext = useCallback(() => {
    if (viewMode === 'day') {
      setCurrentDate((prev) => addMonths(prev, 1));
    } else if (viewMode === 'month') {
      setCurrentDate((prev) => addYears(prev, 1));
    } else {
      setCurrentDate((prev) => addYears(prev, 10));
    }
  }, [viewMode]);

  //  
  const inputClassName = [
    styles.input,
    size === 'sm' && styles['input--sm'],
    size === 'lg' && styles['input--lg'],
    error && styles['input--error'],
  ].filter(Boolean).join(' ');

  //  
  const calendarClassName = [
    styles.calendar,
    size === 'sm' && styles['calendar--sm'],
    size === 'lg' && styles['calendar--lg'],
  ].filter(Boolean).join(' ');

  //   
  const renderCalendarContent = () => (
    <div className={calendarClassName}>
      <CalendarHeader
        currentDate={currentDate}
        viewMode={viewMode}
        onViewModeChange={setViewMode}
        onPrevious={handlePrevious}
        onNext={handleNext}
        minDate={minDate}
        maxDate={maxDate}
        locale={locale}
        size={size}
      />

      {viewMode === 'day' && (
        <CalendarGrid
          currentDate={currentDate}
          selectedDate={selectedDate}
          onSelect={handleSelect}
          minDate={minDate}
          maxDate={maxDate}
          filterDate={filterDate}
          locale={locale}
          size={size}
        />
      )}

      {viewMode === 'month' && (
        <MonthGrid
          currentYear={currentDate.getFullYear()}
          selectedDate={selectedDate}
          onSelect={handleSelect}
          minDate={minDate}
          maxDate={maxDate}
          locale={locale}
          size={size}
        />
      )}

      {viewMode === 'year' && (
        <YearGrid
          currentDecade={getDecadeStart(currentDate.getFullYear())}
          selectedDate={selectedDate}
          onSelect={handleSelect}
          minDate={minDate}
          maxDate={maxDate}
          size={size}
        />
      )}
    </div>
  );

  //  
  if (inline) {
    return (
      <div className={`${styles.inline} ${className || ''}`}>
        {renderCalendarContent()}
      </div>
    );
  }

  return (
    <div className={`${styles.inputWrapper} ${className || ''}`} ref={triggerRef}>
      <input
        ref={inputRef}
        type="text"
        id={id}
        name={name}
        className={inputClassName}
        value={inputValue}
        onChange={handleInputChange}
        onFocus={() => {
          handleOpen();
          onFocus?.();
        }}
        onBlur={handleInputBlur}
        placeholder={placeholder || format}
        disabled={disabled}
        readOnly={readOnly}
        required={required}
        aria-label={ariaLabel}
        aria-describedby={ariaDescribedBy}
        aria-expanded={isOpen}
        aria-haspopup="dialog"
        autoComplete="off"
      />

      {clearable && value && !disabled && !readOnly && (
        <button
          type="button"
          className={styles.clearButton}
          onClick={handleClear}
          aria-label=" "
          tabIndex={-1}
        >
          <X size={10} />
        </button>
      )}

      <span className={styles.inputIcon}>
        <Calendar size={16} />
      </span>

      <CalendarPopover
        isOpen={isOpen}
        triggerRef={triggerRef as React.RefObject<HTMLElement>}
        onClose={handleClose}
      >
        {renderCalendarContent()}
      </CalendarPopover>
    </div>
  );
};

export default DatePicker;
