/**
 *    
 */

import { useState, useCallback, useMemo } from 'react';
import type { CalendarViewMode, DateRange } from '../types';
import { addMonths, addYears, getDecadeStart } from '../utils/dateUtils';

interface UseCalendarStateOptions {
  /**    */
  initialDate?: Date | null;
  /**    */
  initialViewMode?: CalendarViewMode;
  /**   */
  minDate?: Date;
  /**   */
  maxDate?: Date;
  /**    */
  onChange?: (date: Date | null) => void;
  /**   */
  onClose?: () => void;
}

interface UseCalendarStateReturn {
  /**    */
  currentDate: Date;
  /**   */
  selectedDate: Date | null;
  /**    */
  viewMode: CalendarViewMode;
  /**  10  */
  currentDecade: number;
  /**   */
  selectDate: (date: Date) => void;
  /**   */
  clearDate: () => void;
  /**    */
  setCurrentDate: (date: Date) => void;
  /**    */
  setViewMode: (mode: CalendarViewMode) => void;
  /**   */
  goToPrevious: () => void;
  /**   */
  goToNext: () => void;
  /**   */
  goToToday: () => void;
  /**    */
  goToMonth: (month: number) => void;
  /**    */
  goToYear: (year: number) => void;
}

export const useCalendarState = (options: UseCalendarStateOptions = {}): UseCalendarStateReturn => {
  const {
    initialDate = null,
    initialViewMode = 'day',
    onChange,
    onClose,
  } = options;

  //    (  )
  const [currentDate, setCurrentDate] = useState<Date>(
    initialDate ?? new Date()
  );

  //  
  const [selectedDate, setSelectedDate] = useState<Date | null>(initialDate);

  //   (day, month, year)
  const [viewMode, setViewMode] = useState<CalendarViewMode>(initialViewMode);

  //  10 
  const currentDecade = useMemo(() => {
    return getDecadeStart(currentDate.getFullYear());
  }, [currentDate]);

  //  
  const selectDate = useCallback((date: Date) => {
    setSelectedDate(date);
    setCurrentDate(date);
    onChange?.(date);

    // day   
    if (viewMode === 'day') {
      onClose?.();
    }
    // month   day 
    else if (viewMode === 'month') {
      setViewMode('day');
    }
    // year   month 
    else if (viewMode === 'year') {
      setViewMode('month');
    }
  }, [viewMode, onChange, onClose]);

  //  
  const clearDate = useCallback(() => {
    setSelectedDate(null);
    onChange?.(null);
  }, [onChange]);

  //  
  const goToPrevious = useCallback(() => {
    setCurrentDate((current) => {
      if (viewMode === 'day') {
        return addMonths(current, -1);
      } else if (viewMode === 'month') {
        return addYears(current, -1);
      } else {
        return addYears(current, -10);
      }
    });
  }, [viewMode]);

  //  
  const goToNext = useCallback(() => {
    setCurrentDate((current) => {
      if (viewMode === 'day') {
        return addMonths(current, 1);
      } else if (viewMode === 'month') {
        return addYears(current, 1);
      } else {
        return addYears(current, 10);
      }
    });
  }, [viewMode]);

  //  
  const goToToday = useCallback(() => {
    const today = new Date();
    setCurrentDate(today);
    setViewMode('day');
  }, []);

  //   
  const goToMonth = useCallback((month: number) => {
    setCurrentDate((current) => {
      const newDate = new Date(current);
      newDate.setMonth(month);
      return newDate;
    });
    setViewMode('day');
  }, []);

  //   
  const goToYear = useCallback((year: number) => {
    setCurrentDate((current) => {
      const newDate = new Date(current);
      newDate.setFullYear(year);
      return newDate;
    });
    setViewMode('month');
  }, []);

  return {
    currentDate,
    selectedDate,
    viewMode,
    currentDecade,
    selectDate,
    clearDate,
    setCurrentDate,
    setViewMode,
    goToPrevious,
    goToNext,
    goToToday,
    goToMonth,
    goToYear,
  };
};

// ============================================
//   
// ============================================

interface UseRangeStateOptions {
  /**    */
  initialStartDate?: Date | null;
  /**    */
  initialEndDate?: Date | null;
  /**   */
  minDate?: Date;
  /**   */
  maxDate?: Date;
  /**   () */
  minDays?: number;
  /**   () */
  maxDays?: number;
  /**    */
  onChange?: (range: DateRange) => void;
  /**   */
  onClose?: () => void;
}

interface UseRangeStateReturn {
  /**    */
  currentDate: Date;
  /**   */
  startDate: Date | null;
  /**   */
  endDate: Date | null;
  /**   */
  hoverDate: Date | null;
  /**    */
  viewMode: CalendarViewMode;
  /**  10  */
  currentDecade: number;
  /**   ('start' | 'end') */
  selectionMode: 'start' | 'end';
  /**   */
  selectDate: (date: Date) => void;
  /**   */
  clearRange: () => void;
  /**   */
  setHoverDate: (date: Date | null) => void;
  /**    */
  setCurrentDate: (date: Date) => void;
  /**    */
  setViewMode: (mode: CalendarViewMode) => void;
  /**   */
  goToPrevious: () => void;
  /**   */
  goToNext: () => void;
}

export const useRangeState = (options: UseRangeStateOptions = {}): UseRangeStateReturn => {
  const {
    initialStartDate = null,
    initialEndDate = null,
    onChange,
    onClose,
  } = options;

  const [currentDate, setCurrentDate] = useState<Date>(
    initialStartDate ?? new Date()
  );
  const [startDate, setStartDate] = useState<Date | null>(initialStartDate);
  const [endDate, setEndDate] = useState<Date | null>(initialEndDate);
  const [hoverDate, setHoverDate] = useState<Date | null>(null);
  const [viewMode, setViewMode] = useState<CalendarViewMode>('day');
  const [selectionMode, setSelectionMode] = useState<'start' | 'end'>('start');

  const currentDecade = useMemo(() => {
    return getDecadeStart(currentDate.getFullYear());
  }, [currentDate]);

  const selectDate = useCallback((date: Date) => {
    if (viewMode !== 'day') {
      // month/year  
      setCurrentDate(date);
      if (viewMode === 'year') {
        setViewMode('month');
      } else {
        setViewMode('day');
      }
      return;
    }

    if (selectionMode === 'start') {
      setStartDate(date);
      setEndDate(null);
      setSelectionMode('end');
      onChange?.({ startDate: date, endDate: null });
    } else {
      //    
      if (startDate && date < startDate) {
        setStartDate(date);
        setEndDate(startDate);
        onChange?.({ startDate: date, endDate: startDate });
      } else {
        setEndDate(date);
        onChange?.({ startDate, endDate: date });
      }
      setSelectionMode('start');
      onClose?.();
    }
  }, [viewMode, selectionMode, startDate, onChange, onClose]);

  const clearRange = useCallback(() => {
    setStartDate(null);
    setEndDate(null);
    setSelectionMode('start');
    onChange?.({ startDate: null, endDate: null });
  }, [onChange]);

  const goToPrevious = useCallback(() => {
    setCurrentDate((current) => {
      if (viewMode === 'day') {
        return addMonths(current, -1);
      } else if (viewMode === 'month') {
        return addYears(current, -1);
      } else {
        return addYears(current, -10);
      }
    });
  }, [viewMode]);

  const goToNext = useCallback(() => {
    setCurrentDate((current) => {
      if (viewMode === 'day') {
        return addMonths(current, 1);
      } else if (viewMode === 'month') {
        return addYears(current, 1);
      } else {
        return addYears(current, 10);
      }
    });
  }, [viewMode]);

  return {
    currentDate,
    startDate,
    endDate,
    hoverDate,
    viewMode,
    currentDecade,
    selectionMode,
    selectDate,
    clearRange,
    setHoverDate,
    setCurrentDate,
    setViewMode,
    goToPrevious,
    goToNext,
  };
};
