/**
 * CalendarPopover -   
 */

import React, { useEffect, useRef, useState } from 'react';
import { createPortal } from 'react-dom';
import styles from '../styles/Calendar.module.css';

interface CalendarPopoverProps {
  /**   */
  isOpen: boolean;
  /**   ref */
  triggerRef: React.RefObject<HTMLElement>;
  /**   */
  onClose: () => void;
  /**   */
  children: React.ReactNode;
  /**   */
  className?: string;
}

export const CalendarPopover: React.FC<CalendarPopoverProps> = ({
  isOpen,
  triggerRef,
  onClose,
  children,
  className,
}) => {
  const popoverRef = useRef<HTMLDivElement>(null);
  const [position, setPosition] = useState({ top: 0, left: 0 });

  //  
  useEffect(() => {
    if (!isOpen || !triggerRef.current) return;

    const calculatePosition = () => {
      const triggerRect = triggerRef.current!.getBoundingClientRect();
      const scrollY = window.scrollY;
      const scrollX = window.scrollX;

      let top = triggerRect.bottom + scrollY + 4;
      let left = triggerRect.left + scrollX;

      //     
      if (popoverRef.current) {
        const popoverRect = popoverRef.current.getBoundingClientRect();
        const viewportWidth = window.innerWidth;
        const viewportHeight = window.innerHeight;

        //   
        if (left + popoverRect.width > scrollX + viewportWidth) {
          left = triggerRect.right + scrollX - popoverRect.width;
        }

        //    -  
        if (triggerRect.bottom + popoverRect.height > viewportHeight) {
          top = triggerRect.top + scrollY - popoverRect.height - 4;
        }
      }

      setPosition({ top, left });
    };

    //   
    calculatePosition();

    // ,   
    window.addEventListener('resize', calculatePosition);
    window.addEventListener('scroll', calculatePosition, true);

    return () => {
      window.removeEventListener('resize', calculatePosition);
      window.removeEventListener('scroll', calculatePosition, true);
    };
  }, [isOpen, triggerRef]);

  //   
  useEffect(() => {
    if (!isOpen) return;

    const handleClickOutside = (event: MouseEvent) => {
      const target = event.target as Node;

      if (
        triggerRef.current &&
        !triggerRef.current.contains(target) &&
        popoverRef.current &&
        !popoverRef.current.contains(target)
      ) {
        onClose();
      }
    };

    //  
    const timeoutId = setTimeout(() => {
      document.addEventListener('mousedown', handleClickOutside);
    }, 0);

    return () => {
      clearTimeout(timeoutId);
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isOpen, triggerRef, onClose]);

  // ESC  
  useEffect(() => {
    if (!isOpen) return;

    const handleEscape = (event: KeyboardEvent) => {
      if (event.key === 'Escape') {
        onClose();
        triggerRef.current?.focus();
      }
    };

    document.addEventListener('keydown', handleEscape);
    return () => document.removeEventListener('keydown', handleEscape);
  }, [isOpen, triggerRef, onClose]);

  if (!isOpen) return null;

  return createPortal(
    <div
      ref={popoverRef}
      className={`${styles.popover} ${className || ''}`}
      style={{
        top: position.top,
        left: position.left,
      }}
      role="dialog"
      aria-modal="true"
    >
      {children}
    </div>,
    document.body
  );
};

export default CalendarPopover;
